.hero {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding: calc(8rem - 16px) 16px 11.5rem; }
  @media (max-width: 768px) {
    .hero {
      padding: 60px 20px 0 20px; } }
  .hero__header {
    font-weight: 600;
    font-size: 2.5rem;
    line-height: 3.5rem;
    max-width: 780px;
    padding-bottom: 1rem;
    margin: 0; }
  .hero__paragraph {
    font-weight: 300;
    font-size: 1.25rem;
    line-height: 2.25rem;
    color: #52606D;
    max-width: 680px;
    padding-bottom: 3.75rem;
    margin: 0; }
  .hero__link {
    color: #0593FB;
    font-size: 1.25rem;
    font-weight: 500;
    cursor: pointer;
    transition: .4s ease all; }
    .hero__link:hover {
      opacity: .7; }
  .hero__email {
    line-height: 2rem;
    font-size: 1.25rem;
    font-weight: 500;
    color: #0593FB;
    text-decoration: none;
    cursor: pointer;
    position: relative;
    transition: .4s ease opacity; }
    .hero__email:hover:hover {
      opacity: .7; }
    .hero__email::after {
      content: '';
      position: absolute;
      width: 50%;
      height: 2px;
      background: #0593FB;
      bottom: -8px;
      left: 0;
      opacity: .4;
      transition: .4s ease opacity, .4s ease width; }
    .hero__email:hover::after {
      opacity: 1;
      width: 100%; }

.skills {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding: calc(8rem - 16px) 16px 11.5rem; }
  .skills__header {
    width: 100%;
    font-weight: 600;
    font-size: 2.25rem;
    line-height: 3rem;
    display: flex;
    align-items: center;
    position: relative; }
    .skills__header::after {
      content: '';
      position: absolute;
      height: 2px;
      width: 100%;
      background: #CBD2D9;
      bottom: -16px;
      left: 0;
      opacity: 0.5; }
  .skills-list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 100%;
    grid-column-gap: 1.5rem; }
    @media (max-width: 900px) {
      .skills-list {
        grid-template-columns: 1fr; } }
    .skills-list__header {
      font-weight: 600;
      font-size: 2rem;
      line-height: 2.75rem;
      position: relative;
      margin-top: 128px; }
      @media (max-width: 768px) {
        .skills-list__header {
          margin-top: 64px; } }
      .skills-list__header::before {
        content: '';
        position: absolute;
        height: 4px;
        width: 5rem;
        top: -1.5rem;
        left: 0;
        background: #06C847;
        border-radius: 8px; }
    .skills-list__list {
      display: grid;
      grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
      grid-column-gap: 1.5rem;
      padding: 0;
      margin-top: calc(1.5rem + 4px); }
    .skills-list__item {
      color: #52606D;
      list-style-type: none;
      font-size: 1.125rem;
      line-height: 2.5rem;
      padding: 11px 0 11px 30px;
      position: relative; }
      .skills-list__item::before {
        content: '';
        display: block;
        position: absolute;
        top: 22px;
        left: 0;
        height: 18px;
        width: 18px;
        background: url(../assets/img/check-circle.svg); }
        @media (max-width: 768px) {
          .skills-list__item::before {
            top: 18px; } }

html {
  box-sizing: border-box; }
  @media (max-width: 768px) {
    html {
      font-size: 13px; } }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  color: #171F3F;
  background: #fff;
  font-family: 'Poppins', sans-serif; }

.main {
  border: 16px solid #171F3F; }
  @media (max-width: 768px) {
    .main {
      border: none; } }
